.square-item:hover {
  background-color: lightblue;
}

.square-item {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 0.5vw;
  border: 0.2vw dashed #1c87c9;
  text-align: center;
}

input {
  width: 90%;
  margin-top: .5vw;
  height: 2vw;
  font-size: 2vw;
}

.green {
  background-color: lightgreen;
  background-image: url('cross2.png');
  background-size: 100% 100%;
}
